<template>
  <div class="holder" id="viewport">
    <div class="screen"
      @dblclick="replaceImage"
      :style="{
        backgroundColor: backgroundColor,
        height: height + 'px',
        transform: 'scale(' + zoom / 100 + ')'
      }">

      <!--  -->
      <component
        :is="val.type"
        class="layer"
        v-for="(val, i) in widgetStore"
        :key="i"
        :i="i"
        :id="id"
        :val="val"
        :h="height"
        :w="750"
        :playState="playState">
      </component>

      <!--  -->
      <ref></ref>

      <!--  -->
      <control></control>
    </div>
  </div>
</template>

<script>
import ref from "./ref-lines"
import control from "./size-control"
import { move } from "@/mixins"

export default {
  name: "viewport",
  components: {
    ref: ref, // 
    control: control // 
  },

  mixins: [move],

  props: ["zoom"],

  data () {
    return {}
  },

  mounted () {
    // 
    document
      .getElementById("viewport")
      .addEventListener("mousedown", this.handleSelection, false)

    // 
    document.addEventListener(
      "keydown",
      e => {
        e.stopPropagation()
        var target = this.$store.state.activeElement

        // 
        if (e.keyCode === 37 && target.left) {
          target.left -= 1
          return
        }
        // 
        if (e.keyCode === 38 && target.top) {
          e.preventDefault()
          target.top -= 1
          return
        }
        // 
        if (e.keyCode === 39 && target.left) {
          target.left += 1
          return
        }

        // 
        if (e.keyCode === 40 && target.top) {
          e.preventDefault()
          target.top += 1
        }
      },
      true
    )
  },

  methods: {
    handleSelection (e) {
      var target = e.target
      var type = target.getAttribute("data-type")

      if (type) {
        var index = target.getAttribute("data-index")

        // 
        this.$store.commit("select", {
          type: type,
          index: index ? parseInt(index) : -1
        })

        //  page 
        var target = this.$store.state.activeElement
        if (target.belong === "page" && type !== "braid-bg") {
          this.initmovement(e) //  mixins
        }
      } else {
        // 
        this.$store.commit("select", {
          type: "page",
          index: -1
        })
      }
    },

    // FIXME: indexOf('image') 
    replaceImage (e) {
      if (this.id.toLowerCase().indexOf("image") > -1) {
        $communicator.$emit("upload", payload => {
          this.$store.commit("replaceImage", payload)
        })
      }
    }
  },

  computed: {
    // 
    widgetStore () {
      return this.$store.state.widgets.filter(item => item.belong === "page")
    },

    // 
    height () {
      return this.$store.state.page.height
    },

    // 
    backgroundColor () {
      return this.$store.state.page.backgroundColor
    },

    // id
    id () {
      var type = this.$store.state.type
      var index = this.$store.state.index
      index = index >= 0 ? index : ""
      return type + index
    },

    // 
    playState () {
      return this.$store.state.playState
    }
  }
}
</script>

<style scoped>
.holder {
  height: 100%;
  overflow: auto;
  font-size: 0;
  border: 1px solid #f5f5f5;
  border-width: 0 1px;
  background-image: linear-gradient(
      45deg,
      #f5f5f5 25%,
      transparent 0,
      transparent 75%,
      #f5f5f5 0
    ),
    linear-gradient(
      45deg,
      #f5f5f5 25%,
      transparent 0,
      transparent 75%,
      #f5f5f5 0
    );
  background-position: 0 0, 13px 13px;
  background-size: 26px 26px;
}
.screen {
  width: 750px;
  margin: 25px auto;
  transform-origin: center top;
  position: relative;
}
</style>



